    -webkit-animation: change 2s ease-in-out 1;
            animation: change 2s ease-in-out 1; }

@-webkit-keyframes change {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6); }
  70% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes change {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6); }
  70% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }
